import React from "react";
import { useState } from "react";
import { WordleHeader } from "./WordleHeader";
import { TextField, Stack, Button } from '@mui/material';
import { Words } from './Words';
import { Typography } from '@mui/material';

export const WordleBox = () => {

    const [included, setIncluded] = useState("");

    const [excluded, setExcluded] = useState("");

    const [output, setOutput] = useState([]);

    const handleFindButton = () => {
        console.log(included.join(", "));
    }

    const runEngine = () => {
        var includedArray = included.toLowerCase().split('');
        var excludedArray = excluded.toLowerCase().split('');
        var output = [];
        output = Words.filter(word => includedArray.every(char => word.includes(char)));
        output = output.filter(word => excludedArray.every(char => !word.includes(char)));
        console.log(output);
        setOutput(output);
    }

    const handleIncludes = (e) => {
        var input = e.target.value;
        input = input.replace(/ /g, '');
        input = input.replace(/,/g, '');
        input = input.toUpperCase();
        console.log("Included : ", input);
        if (included.slice(0, -1) === input) {
            setIncluded(input);
        } else {
            if (included.includes(input[input.length - 1])) {
                console.log("Letters must be unique.")
            } else {
                var newIncluded = included.concat(input[input.length - 1]);
                setIncluded(newIncluded);
            }
        }
    }

    const handleExcludes = (e) => {
        var input = e.target.value;
        input = input.replace(/ /g, '');
        input = input.replace(/,/g, '');
        input = input.toUpperCase();
        console.log("Excluded : ", input);
        if (excluded.slice(0, -1) === input) {
            setExcluded(input);
        } else {
            if (excluded.includes(input[input.length - 1])) {
                console.log("Letters must be unique.")
            } else {
                var newExcluded = excluded.concat(input[input.length - 1]);
                setExcluded(newExcluded);
            }
        }
    }


    return (
        <>
            <div className="WordleBox">

                <WordleHeader />
                <br /> <br />
                <Stack
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                    spacing={4}
                >
                    <TextField
                        name="included"
                        id="standard-basic"
                        label="Includes"
                        variant="standard"
                        inputProps={{ maxLength: 12 }}
                        value={included.split("").join(", ")}
                        onChange={handleIncludes}
                    />

                    <TextField
                        name="excluded"
                        id="standard-basic"
                        label="Excludes"
                        variant="standard"
                        inputProps={{ maxLength: 12 }}
                        value={excluded.split("").join(", ")}
                        onChange={handleExcludes}
                    />

                    <Button variant="contained" onClick={runEngine}> Filter </Button>
                    <Typography variant="body1" gutterBottom>
                        {output.join(", ")}
                    </Typography>
                </Stack>


                <br />
            </div>
        </>
    );
}